import { motion } from "../../node_modules/framer-motion/dist/framer-motion";

export default function Ksos() {
  return (
    <motion.div>
      <div
        className="home"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <div className="reset">
          <iframe
            title="niris"
            src="https://www.niris.tv/blog/weekly-reset"
            frameBorder="0"
            scrolling="auto"
            height="100%"
            width="100%"
          ></iframe>
        </div>
      </div>
      <div className="footer"></div>
    </motion.div>
  );
}
